  }, {"internalType": "uint256", "name": "amount", "type": "uint256"}],
  "name": "transferFrom",
  "outputs": [{"internalType": "bool", "name": "", "type": "bool"}],
  "stateMutability": "nonpayable",
  "type": "function"
}, {"stateMutability": "payable", "type": "receive"}]

var contractAddress = '0xf657d69c62c39be9da87d0fb7625b2f730b5af8f';

export default {
  name: "Staking",
  data: function () {
    return {
      isObservationEnabled: false,
      user: null,
      contract: null,
      requiredSize: 250000000,
      size: 0,
      answer: '',
      result: ''
    }
  },
  methods: {
    beautifyWallet: function (address) {
      return address.slice(0, 10) + '...' + address.slice(-4)
    },
    format: function (number) {
      return new Intl.NumberFormat('en-US', {
        minimumFractionDigits: 0,
        maximumFractionDigits: 0
      }).format(number)
    },
    submitAnswer: async function () {
    },
    start: async function () {
      /*****************************************/
      /* Detect the MetaMask Ethereum provider */
      /*****************************************/

      // this returns the provider, or null if it wasn't detected
      //const provider = await detectEthereumProvider();
      const provider = window.ethereum;

      if (provider) {
        this.startApp(provider); // Initialize your app
      }


      /***********************************************************/
      /* Handle user accounts and accountsChanged (per EIP-1193) */
      /***********************************************************/

      window.ethereum
          .request({method: 'eth_accounts'})
          .then(this.handleAccountsChanged)
          .catch((err) => {
            // Some unexpected error.
            // For backwards compatibility reasons, if no accounts are available,
            // eth_accounts will return an empty array.
            console.error(err);
          });
    },
    startApp: function (provider) {
      // If the provider returned by detectEthereumProvider is not the same as
      // window.ethereum, something is overwriting it, perhaps another wallet.
      if (provider !== window.ethereum) {
        console.error('Do you have multiple wallets installed?');
      }

      //connect to the contract
      let web3js = new Web3(provider);
      this.contract = new web3js.eth.Contract(abi, contractAddress);

      web3js.eth.getChainId().then((id) => {
        if (id != 1) {
          alert('Not connected to Ethereum Mainnet !' + id);
        } else {
          //recursively get total supply
          const refreshTotalSupply = () => {
            // eslint-disable-next-line
            this.getSize();
          }

          refreshTotalSupply();
        }
      });
    },
    getSize: function () {
      return this.contract.methods.balanceOf(this.user).call().then(res => {
        this.size = res / 10 ** 9
      })
    },
    handleAccountsChanged: function (accounts) {
      if (accounts.length === 0) {
        this.connect();
      } else if (accounts[0] !== this.user) {
        this.user = accounts[0];
        this.getSize();
      }
    },
    connect() {
      window.ethereum
          .request({method: 'eth_requestAccounts'})
          .then(this.handleAccountsChanged)
          .catch((err) => {
            if (err.code === 4001) {
              // EIP-1193 userRejectedRequest error
              // If this happens, the user rejected the connection request.
              console.log('Please connect to MetaMask.');
            } else {
              console.error(err);
            }
          });
    }
  },
  mounted() {
    this.start()
    window.ethereum.on('accountsChanged', this.handleAccountsChanged);
  }
}
</script>

